.masonry {
  -webkit-column-count: 4;
          column-count: 4;
  margin: 0.5em auto;
  margin-top: 60px;
  max-width: 100%;
  grid-column-gap: 0.5em;
  -webkit-column-gap: 0.5em;
          column-gap: 0.5em;
}

/* The Masonry Brick */
.item {
  background: #fff;
  padding: 0.5em;
  margin: 0 0 1em;
  border-radius: 5px;
}
.item:hover{
  background-color: rgb(221, 221, 221);
  cursor: zoom-in;
}
.item img{
  width: 100%;
  border-radius: 5px;
}

/* Masonry on large screens */
@media only screen and (min-width: 1024px) {
  .masonry {
    -webkit-column-count: 4;
            column-count: 4;
  }
}

/* Masonry on medium-sized screens */
@media only screen and (max-width: 1023px) and (min-width: 768px) {
  .masonry {
    -webkit-column-count: 3;
            column-count: 3;
  }
}

/* Masonry on small screens */
@media only screen and (max-width: 767px) and (min-width: 540px) {
  .masonry {
    -webkit-column-count: 2;
            column-count: 2;
  }
}
.modalContainer{
    position: fixed;
    z-index: 1;
    left: 0;
    top: 55px;
    width: 100%;
    height: 100%;
    background: rgb(238, 238, 238);
    padding: 30px; 
}
.flex{
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}
.modalContainer .inicio{
    position: fixed;
    text-decoration: none;
    color:black;
    font-size: 15px;
    font-weight: bold;
    height: auto;
    padding: 5px 10px;
}
.modalContainer .inicio i{
    padding: 0 10px;
}
.modalContainer .inicio:hover{
    background-color: rgb(194, 194, 194);
    border-radius: 20px;
}
.modalContainer .modal{
    position: relative;
    width: 70%;
    height: auto;
    padding: 10px;
    background: white;
    border-radius: 5px;
    margin:auto;
    margin-top: 0;
}
.headerModal a{
